.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 80%;
  height: 80%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 10%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  overflow-y: scroll;
}
.ModalBtn {
  margin: 5% 0;
  width: 100%;
}
@media (min-width: 600px) {
  .Modal {
    width: 60%;
    left: calc(50% - 30%);
  }
}
